import { useState } from "react";

import "./ProdutosList.css";

const ProdutosList = ({
  Produtos,
  ProdutosBackup,
  Categorias,
  setProdutos
}) => {
  const inputPesquisaChange = (event) => {
    let novoArray = ProdutosBackup.filter(
      (produto) =>
        produto.Nome.toUpperCase().includes(event.target.value.toUpperCase()) ||
        produto.Ingredientes.toUpperCase().includes(
          event.target.value.toUpperCase()
        ) ||
        produto.Categoria.Nome.toUpperCase().includes(
          event.target.value.toUpperCase()
        )
    );

    // 'setProdutosBackup(Produtos);
    setProdutos(novoArray);
  };

  function TituloCategoria(props) {
    const arr = Produtos.filter(
      (produto) => produto.Categoria.id === props.categoria.id
    );

    return arr.length ? (
      <h3 className="titulo-categoria">{props.categoria.Nome}</h3>
    ) : (
      ""
    );

    /*
    if (arr.length) {
      return <h3 class="titulo-categoria">{props.categoria.Nome}</h3>;
    } else {
      return "";
    }*/
  }

  /*
  useEffect(() => {
    setProdutosFiltrados(Produtos);
  }, []);
*/

  return (
    <>
      <div class="containerInput">
        <input
          class="inputPesquisa"
          placeholder="Pesquisar por produto ou ingrediente"
          type="search"
          onChange={inputPesquisaChange}
        />
      </div>

      {Categorias.map((categoria) => (
        <>
          <TituloCategoria categoria={categoria} />

          {Produtos.filter(
            (produto) => produto.Categoria.id === categoria.id
          ).map((produto) => (
            <div className="card-item">
              <div class="div-imagem">
                <img
                  className="imagemProduto"
                  alt="imagem do produto"
                  src={produto.Imagem.url}
                />
              </div>
              <div className="coluna">
                <div className="produtoNome">{produto.Nome}</div>
                <div className="produtoIngredientes">
                  {produto.Ingredientes}
                </div>
                <div className="linhaPreco">
                  <div className="preco">R$ {produto.Preco}</div>
                </div>
              </div>
            </div>
          ))}
        </>
      ))}
      <p className="mensagem-imagens">As imagens s√£o meramente ilustrativas</p>
    </>
  );
};

export default ProdutosList;
