import "./styles.css";
import React, { useEffect, useState } from "react";
import Cabecalho from "./components/Cabecalho/Cabecalho";

import Conteudo from "./components/Conteudo/Conteudo";

const App = () => {
  const [Produtos, setProdutos] = useState([]);
  const [ProdutosBackup, setProdutosBackup] = useState([]);
  const [Categorias, setCategorias] = useState([]);
  const [isLoading, setLoading] = useState(true);

  const getAllProdutos = async () => {
    try {
      const response = await fetch(
        "https://x8ki-letl-twmt.n7.xano.io/api:p7GSSb0k/produtos"
      );
      const responseJson = await response.json();

      setProdutos(responseJson);
      setProdutosBackup(responseJson);
    } catch (error) {
      console.error(error);
    } finally {
      setLoading(false);
    }
  };

  const getAllCategorias = async () => {
    try {
      const response = await fetch(
        "https://x8ki-letl-twmt.n7.xano.io/api:xR9a0v67/categorias"
      );
      const responseJson = await response.json();

      setCategorias(responseJson);
    } catch (error) {
      console.error(error);
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    getAllProdutos();
  }, []);

  useEffect(() => {
    getAllCategorias();
  }, []);

  useEffect(() => {
    document.title = "Lanchonete Paladar";
  }, []);

  return (
    <div className="app">
      <Cabecalho />

      {isLoading ? (
        <div className="loading">
          <div className="loader"> </div>
        </div>
      ) : (
        <Conteudo
          Produtos={Produtos}
          ProdutosBackup={ProdutosBackup}
          Categorias={Categorias}
          setProdutos={setProdutos}
        />
      )}
    </div>
  );
};

export default App;
